// src/router/index.ts

import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router";
import Project from "@/views/Project.vue";
import Home from "@/views/Home.vue";
import Main from "@/views/Main.vue";
import Properties from "@/views/Properties.vue";
import Stakeholders from "@/views/Stakeholders.vue";
import ChartView from "@/views/ChartView.vue";

import {
  clearProjectDataCacheFromRoute,
  getProjectTypeFromRoute,
  getRouteTypeValue,
  hideOverlay,
  loadAll,
  PROJECT_TYPE_ROUTES,
  setDocumentTitle,
  showOverlay,
} from "@/store";

const routes: Array<RouteRecordRaw> = [
  { name: "home", path: "/", component: Home },
  { name: "global-view", path: "/project/global-view", component: Main },
  { name: "project", path: "/project/", component: Project },
  {
    name: "property-view",
    path: "/project/property-view",
    component: Properties,
    props: true,
  },
  {
    name: "stakeholder-view",
    path: "/project/stakeholder-view",
    component: Stakeholders,
    props: true,
  },
  {
    name: "chart-view",
    path: "/project/chart-view",
    component: ChartView,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach(async (to, from, next) => {
  showOverlay();
  next();
});

router.afterEach(async (to, from) => {
  if (to.path.startsWith("/project")) {
    const toProjectType = getProjectTypeFromRoute(to);
    const fromProjectType = getProjectTypeFromRoute(from);
    const toProjectId = getRouteTypeValue(to);
    const fromProjectId = getRouteTypeValue(from);
    const isSameProject =
      toProjectType === fromProjectType && toProjectId === fromProjectId;
    if (!isSameProject) {
      await loadProject();
    }
  } else {
    if (typeof to.meta?.title === 'string') {
      setDocumentTitle(to.meta.title);
    } else {
      setDocumentTitle("My Choice");
    }    
  }

  hideOverlay();
});

// In Vue 3, there's no router.onReady. You can call loadProject after app is mounted.
export const loadProject = async () => {
  const route = router.currentRoute.value;
  if (
    route.query[PROJECT_TYPE_ROUTES.ICO] ||
    route.query[PROJECT_TYPE_ROUTES.GOOGLE_SPREADSHEET] ||
    route.query[PROJECT_TYPE_ROUTES.NEXTCLOUD] ||
    route.query["demo"]
  ) {
    try {
      await loadAll(route);
    } catch (e) {
      console.info("LOAD FAIL, RETRY WITHOUT CACHE!");
      clearProjectDataCacheFromRoute(route);
      await loadAll(route);
    }
  }
};

export default router;
